@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Merriweather Sans', sans-serif;
}

body {
  margin: 30px 30px
}

h2 {
  color: green;
  font-style: italic;
}

li {
  color: grey;
  padding-top: 3px;
  font-size: 15px;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.success {
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}


